@font-face {
  font-family: "BeVietnamPro-Light";
  src: url("../../fonts/BeVietnamPro-Thin.ttf");
}
header {
  width: 100%;
  position: fixed;
  z-index: 999;
}
header .header-inner {
  width: 100%;
  padding: 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 10;
}
header .header-inner .header-part {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 50%;
}
header .header-inner .header-item {
  font-family: "BeVietnamPro-Light";
  font-size: 20px;
  text-transform: uppercase;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  padding: 4px 6px;
  border-radius: 10px;
  color: white;
  text-decoration: none;
  z-index: 9999;
}
header .header-inner .header-item:hover {
  background-color: #F66C5E;
  color: #222;
  font-weight: bold;
}
header .header-inner img {
  width: 60px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
header .header-inner img:hover {
  opacity: 0.5;
}