.about {
  background-color: #222;
  padding: 50px 0;
}
.about img {
  width: 300px;
  border-radius: 50%;
  margin-right: 50px;
}
.about .about-inner {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.about .about-inner .about-title {
  font-size: 32px;
}
.about .about-inner .about-text a {
  color: white;
}